html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  height: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

header {flex: 0 0 auto;} main {flex: 1 0 auto; margin-top: 150px; 
  @include medium {margin-top: 100px;} 
  @include small-tablet {margin-top: 60px;} 
  @include mobile {margin-top: 40px;}
}footer {flex: 0 0 auto;}

body {
  position: relative;
  overflow-anchor: none;
  margin: 0;
  min-width: 320px;
  height: 100%;
  font-family: "Overpass", sans-serif;
  font-size: 16px;
  color: #fff;
  background-color: var(--background-color);
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}
.site-container {
  -webkit-text-size-adjust: 100%;
  display: flex;
  flex-direction: column;
  height: 100%;
}

// Reset's
.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}
.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}
.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: transparent;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

// Primary-Classes
.container {
  position: relative;
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1167px;
}
.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}
.page--ios .dis-scroll {
  position: relative;
} 
.to-top {
  position: fixed;
  right: 2%;
  bottom: 5%;
  z-index: 200;
  display: inline-flex; 
  align-items: center;
  justify-content: center;
  border-radius: 100%;
  width: 48px;
  height: 48px;
  box-shadow: 0px 5px 30px rgba(0, 0, 0 , 0.1)   ;
  background-color: var(--color-white);
}
