/* stylelint-disable */

@mixin for-desktop {
  @media (min-width: (1025px)) {
    @content;
  }
}

@mixin lg-desktop {
  @media (max-width: (1650px)) {
    @content;
  }
}

@mixin big-desktop {
  @media (max-width: (1440px)) {
    @content;
  }
}

@mixin medium {
  @media (max-width: (1140px)) {
    @content;
  }
}
@mixin tablet {
  @media (max-width: (1023.68px)) {
    @content;
  }
}

@mixin small-tablet {
  @media (max-width: (768px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (576px)) {
    @content;
  }
}
@mixin checkbox-media {
  @media (min-width: (768px)) {
    @content;
  }
}
